.hashtagcapaignwrapper .form-control-lg{
    padding-right: 4.5rem;
}

.hashtag-media{
    max-height: 450px;
    overflow-x: auto;
}

.hashtag_social_share{
    left: -20px;
}
.hashtag_social_share .btn{
    transition: transform .3s;
}

.hashtag_social_share .btn:hover {
    transform: scale(1.1);
}

.hashtag_social_share .btn[data-type="facebook"]{
    background-color: #1777f2;
}
.hashtag_social_share .btn[data-type="twitter"]{
    background-color: #29a9e1;
}
.hashtag_social_share .btn[data-type="linkedin"]{
    background-color: #0e76a8;
}
.hashtag_social_share .btn[data-type="envelope"]{
    background-color: #545454;
}
.hashtag_theme_wrapper {
    padding-bottom: calc(100vh - 340px);
    position: relative;
}
.hashtag_theme_preview {
    position: absolute;
    transform: scale(0.5);
    width: 200%;
    margin-left: -50%;
    min-height: 500px;
    height: 200%;
    margin-top: -32vh;
    margin-bottom: -32vh;
    top: 0;
    left: 0;
}

.hashtag_theme_preview iframe{
    border: none;
    width: 100%;
    height: 100%;
}



@media (min-width: 992px){
.hashtag_campaign_view {
    height: calc(100vh - 220px);
    overflow: auto;
    min-height: 475px;
}
}