
.apexchartsanalyticsOverview .apexcharts-tooltip.apexcharts-theme-light {
    border: none!important;
    background: var(--bs-body-bg) !important;
    -webkit-box-shadow: 0 0 50px 0 rgba(82,63,105,.15)!important;
    box-shadow: 0 0 50px 0 rgba(82,63,105,.15)!important;
}
.apexchartsanalyticsOverview .apexcharts-tooltip.apexcharts-theme-light .apexcharts-tooltip-title {
    background: rgba(var(--bs-dark-rgb), 0.05)!important;
    border-bottom: none!important;
}

.apexchartsanalyticsOverview .apexcharts-xaxistooltip {
    background: var(--bs-body-bg) !important;
    border-radius: 0.42rem!important;
    -webkit-box-shadow: 0 0 15px 0 rgba(var(--bs-dark-rgb),.15)!important;
    box-shadow: 0 0 15px 0 rgba(var(--bs-dark-rgb),.15)!important;
    border: 0!important;
}

.apexchartsanalyticsOverview .apexcharts-xaxistooltip-bottom:after,
.apexchartsanalyticsOverview .apexcharts-xaxistooltip-bottom:before {
    border-bottom-color: var(--bs-body-bg) !important;
}

.apexchartsanalyticsOverview .apexcharts-tooltip-y-group,
.apexchartsanalyticsOverview .apexcharts-xaxistooltip-text{
    color: var(--bs-gray-800);
}

.apexchartsanalyticsOverview .apexcharts-legend-text {
    margin-left: -12px;
    color: var(--bs-gray-800) !important;
}
.apexchartsanalyticsOverview .apexcharts-text tspan{
    fill: var(--bs-gray-800) !important;
}
.apexchartsanalyticsOverview .apexcharts-graphical line{
    stroke: var(--bs-gray-300) !important;
}

.topTagsWrapper, .assetsBySource, .top_influencercreator {
    height: 310px;
}


.assetsType .apexcharts-datalabels-group text{
    fill:var(--bs-gray-800)
}
.assetsType .apexcharts-tooltip-y-group,
.assetsType .apexcharts-xaxistooltip-text{
    color: #fff;
}

.assetsType .apexcharts-tooltip-y-group {
    padding: 5px 0px 5px 15px;
    display: flex;
    flex-direction: column;
    position: relative;
}
.assetsType .apexcharts-tooltip-y-group:before {
    content: "";
    height: 80%;
    width: 3px;
    background-color: #fff;
    margin-right: 10px;
    position: absolute;
    top: 10%;
    left: 0;
    border-radius: 1px;
}

.assetsType .apexcharts-tooltip-text {
    display: flex;
}

.assetsType .apexcharts-tooltip-text-goals-value, .assetsType .apexcharts-tooltip-text-y-value, .assetsType .apexcharts-tooltip-text-z-value {
    display: inline-block;
    margin-left: 0;
    font-weight: 600;
}

.assetsType .apexcharts-tooltip-series-group.apexcharts-active, .assetsType .apexcharts-tooltip-series-group:last-child {
    padding-bottom: 0px;
    min-width: 90px;
}

.assetsType .apexcharts-pie-series path{
    stroke: var(--bs-gray-200) !important;
}

.top_per_asset_content:after  {
    content: ''; 
    display: block; 
    position: absolute; 
    bottom: 0; 
    left: 0; 
    width: 100%; 
    background: -moz-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(var(--bs-white-rgb),1) 100%);
    background: -webkit-linear-gradient(top, rgba(var(--bs-white-rgb),0) 0%,rgba(var(--bs-white-rgb),1) 100%);
    background: linear-gradient(to bottom, rgba(var(--bs-white-rgb),0) 0%,rgba(var(--bs-white-rgb),1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=0 ); 
    height: 50px;
}
.top_per_asset_content{
    max-height: 50px;
    min-height: 50px;
    opacity: 1 !important;
}

.user_profile_items{
    max-width: 300px;
    width: 100%;
}

@media (min-width: 991px){
.topperform_content{
    max-height: 435px;
    overflow-y: auto;
}
.user_profile_items{
    max-width: 160px;
}
.top_influencercreator{
    height: calc(22vw);
}
}
@media (min-width: 1280px){
    .top-performing-modal .modal-dialog{
        max-width: 1000px;
    }

}
@media (min-width: 1600px){
    .col-top-influencer {
        flex: 0 0 auto;
        width: 37.5%;
    }
    .col-top-assets {
        flex: 0 0 auto;
        width: 62.5%;
    }
}

@media (max-width: 767px){
    .apexchartsanalyticsOverview .apexcharts-legend-series{
        margin: 0px 5px !important;
    }
}

@media (max-width: 480px){
    .apexchartsanalyticsOverview .apexcharts-legend-series{
        margin: 0px 2px !important;
    }
}