.embed_code .dropdown-menu{
    min-width: 90px;
}

.add_fillter_btn .accordion-button {
    background-color: transparent;
    max-width: 130px;
}

.add_fillter_btn .accordion-button::after,
.disable_arrow .accordion-button::after{
    display: none;
}

.add_fillter_btn .accordion-button:hover,
.add_fillter_btn .accordion-button:not(.collapsed) {
    background-color: var(--bs-accordion-active-bg);
}
.filter_list .card-body{
    padding: 0;
}

.widget_view{
    height: calc(100vh - 275px);
    min-height: 280px;
    overflow-y: auto;
}

.display_view{
    height: calc(100vh - 230px);
    min-height: 485px;
    overflow-y: auto;
}