.syncPro_modal_wrapper{
    display: flex;
    align-items: flex-start;
}

.syncPro_modal {
    padding-top: 10%;
    padding-bottom: 10%;
}

@media (max-width: 991px) {
    .syncPro_modal {
        padding-top: 2%;
        padding-bottom: 2%;
    }
}

@media (max-width: 575px) {
    .syncPro_modal_wrapper{
        width: 100%;
        flex-direction: column;
        align-items: stretch;
        border-bottom: 1px solid var(--bs-border-color);
        padding-bottom: 15px;
    }
    .syncPro_modal_wrapper:last-child{
        border-bottom: none;
    }

    .syncPro_modal {
        padding-top: 0;
        padding-bottom: 0;
    }
}