.auth_bg{
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.auth_form_area{
    max-width: 420px;
    margin: 0 auto;
    width: 100%;
}

.view_pass{
    right: -10px;
}

@media (min-width: 992px){
    .auth_form_area{
        max-width: 450px;
    }
}

@media (min-width: 1600px){
    .auth_form_area{
        max-width: 470px;
    }
}