.editMedia_modal .FIE_root{
    height: calc(100vh - 46px) !important;
}
.editMedia_modal .ZdrwB {
    background-color: var(--bs-primary);
    color: rgb(255, 255, 255);
    padding: 15px 12px;
}

.editMedia_modal .ZdrwB:hover {
    background-color: var(--bs-primary);
}

.editMedia_modal .kPEApq{
    background-color: var(--bs-body-bg);
}

.editMedia_modal .fBEhPu,
.editMedia_modal .isHcCM{
    background-color: var(--bs-light);

}

.editMedia_modal .aRBme {
    width: 24px;
    height: 24px;
    padding: 0px;
}

.editMedia_modal .aRBme svg{
    width: 16px;
    height: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.editMedia_modal .fiqNQN {
    color: var(--bs-gray-700);
    font-size: 14px;
}

.editMedia_modal .FIE_spinner-label{
    display: none;
}

.editMedia_modal .FIE_spinner {
    font-size: 0px;
    margin: 34px auto;
    width: 34px;
    height: 34px;
    border-top: 3px solid var(--bs-gray-700);
    border-radius: 50%;
    animation: spin 0.8s linear infinite;
}

.editMedia_modal .FIE_spinner:before,
.editMedia_modal .FIE_spinner:after{
    content: none;
}

.editMedia_modal .hkJRfE {
    background: rgba(var(--bs-body-bg),1.0);
}

.editMedia_modal .fiqNQN{
    line-height: 20px;
}
.editMedia_modal .FIE_topbar-reset-button svg {
    margin-top: 4px;
    width: 18px;
    height: 18px;
}
.editMedia_modal .FIE_text-tool-options,
.editMedia_modal .FIE_text-bold-option,
.editMedia_modal .FIE_text-italic-option,
.editMedia_modal .FIE_annotation-option-triggerer,
.editMedia_modal .FIE_annotation-option-triggerer {
    cursor: pointer;
    padding: 0px 3px;
    min-width: 30px;
    height: 30px;
    margin: 0px 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 2px;
}

body .SfxSlider-thumb,
body .SfxSlider-Track,
body .SfxSlider-rail{
    color: var(--bs-primary);
}

.editMedia_modal .FIE_topbar-buttons-wrapper,
.editMedia_modal .FIE_topbar{
    flex-direction: row-reverse;
}

.editMedia_modal .FIE_topbar-buttons-wrapper{
    align-items: center;
}

.editMedia_modal .FIE_topbar{
    padding: 12px 12px 0px;
}

.editMedia_modal .FIE_topbar-save-wrapper{
    width: 100px !important;
    height: 34px;
    margin-right: 0;
    margin-left: 12px;
}
.editMedia_modal .FIE_tools-bar-wrapper{
    max-height: 120px;
    padding: 12px 0px 0px;
}
.editMedia_modal .FIE_hue-option-label,
.editMedia_modal .FIE_saturation-option-label,
.editMedia_modal .FIE_value-option-label{
    margin-bottom: 10px;
}

.editMedia_modal .FIE_annotation-option-triggerer[title="Text alignment"] svg{
    margin-top: 8px;
}

body .SfxModalTitle-root {
    position: relative;
    color: rgb(55, 65, 75);
    padding: 30px 15px 15px 15px;
}
body .SfxModalActions-root {
    padding: 12px 16px 30px 16px;
}

body .FIE_save-modal{
    width: 100% !important;
    max-width: 420px;
}

body .FIE_resize-x-label{
    padding: 6px;
    font-size: 20px;
}

body .SfxInput-root{
    border: none;
    background: none;
    background-color: transparent;
    margin: 0px;
    width: 100%;
    height: auto;
    padding: 0;
}

body .SfxModalContent-root {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    width: 100%;
}

body .FIE_save-file-name-input{
    position: relative;
    flex: 1 1 auto;
    width: 1%;
    min-width: 0;
}
body .SfxInput-root:hover,
body .SfxInput-root:focus-within,
body .SfxInput-root:focus{
    border: none;
    background: none;
    background-color: transparent !important;
}
body .FIE_resize-tool-options{
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
}
body .FIE_resize-ratio-locker{
    margin-right: 0;
}
body .SfxModalContent-root .SfxLabel-root{
    margin-bottom: 10px;
}
body .SfxModal-Container{
    background-color: var(--bs-body-bg);
}

body .SfxMenu-root{
    background-color: var(--bs-body-bg);
    border-radius: 4px;
    padding: 6px;
}

body .SfxMenuItem-root{
    color: var(--bs-dark);
    text-decoration: none;
    background-color: var(--bs-body-bg);
    margin-bottom: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding: 7px 12px;
    transition: none;
    border-radius: 4px;
    outline: none !important;
}
body .SfxMenuItem-root:focus-within, body .SfxMenuItem-root:focus, body .SfxMenuItem-root:hover {
    background-color: var(--bs-light);
}

body .SfxInput-Base,
body .SfxInput-Base:focus-within,
body .SfxInput-Base:focus,
body .SfxSelect-Container {
    min-width: 90px;
    display: block;
    width: 100%;
    height: auto;
    line-height:27px;
    letter-spacing:normal;
    margin:0px;
    padding: 0.25rem 0.5rem;
    font-weight:600;
    font-size: 13px;
    color: var(--bs-dark);
    border: 1px solid var(--bs-border-color-translucent);
    border-radius: 4px;
}

body .SfxModalContent-root .SfxInput-Base{
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

.editMedia_modal .SfxSelect-Container{
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
}

body .SfxSelect-Container{
    background-color: var(--bs-light);
    cursor: pointer;
    width: 100px;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    margin-left: -1px;
}

body .FIE_annotations-options .SfxSelect-Container {
    width: 110px;
    line-height: 20px;
    padding: 0.2rem 0.5rem;
    margin: 0 5px;
}  

body .FIE_annotations-options .FIE_text-size-option{
    padding: 0;
    width: 64px;
}

body .FIE_annotations-options .FIE_text-size-option .SfxInput-Base{
    min-width: 60px;
    display: block;
    width: 100%;
    height: auto;
    line-height: 22px;
    letter-spacing: normal;
    margin: 0px;
    padding: 0.2rem 0.5rem;
}

body .SfxSelect-Label{
    color: var(--bs-gray-700);
}

body .SfxRotationSlider-list{
    margin: auto -8px;
}

body .FIE_carousel-next-button{
    background: linear-gradient(270deg, rgb(var(--bs-white-rgb)) 1.56%, rgba(var(--bs-white-rgb), 0.93) 70%, rgba(var(--bs-white-rgb), 0) 100%);
}

body .FIE_carousel-prev-button{
    background: linear-gradient(90deg, rgb(var(--bs-white-rgb)) 1.56%, rgba(var(--bs-white-rgb), 0.93) 70%, rgba(var(--bs-white-rgb), 0) 100%);
}

@media (min-width: 992px){
.editMedia_modal .FIE_topbar-center-options{
    margin-left:300px
}
}
@media (max-width: 480px){
.editMedia_modal .FIE_topbar-center-options{
    display: none;
}

.editMedia_modal .FIE_text-tool-options {
    flex-wrap: nowrap;
    overflow-x: auto;
    height: 38px;
}
}