.socialAdsMedia{
    height: 480px;
    overflow-y: auto;
}

.adsRightArrow .svg-icon svg {
    width: 40px;
    height: 60px;
}

.adsImage{
    height: 300px;
}
.adsImage .rounded-1{
    border-radius: 0px !important;
}

.adsImage svg{
    height: 90px;
    width: 90px;
}

.socialAdsPreview{
    width: 320px;
    margin: 0 auto;
}

.gallery_view {
    height: calc(100vh - 315px);
    min-height: 393px;
    overflow-y: auto;
}
.print_gallery{
    height: calc(100vh - 305px);
    min-height: 525px;
    overflow-y: auto;
    padding-right: 5px;
}
@media (min-width: 1280px) {
    .sociaPublish_wrap {
        height: auto;
    }
}
@media (min-width: 992px) {
.socialAdsPreview {
    max-height: calc(100vh - 165px);
    overflow-y: auto;
}
.sociaPublish_wrap {
    height: calc(100vh - 290px);
    overflow-y: auto;
}

.print_aside_inner {
    height: calc(100vh - 370px);
    overflow-y: auto;
    min-height: 460px;
    padding-right: 5px;
}

}
@media (max-width: 991px) {
    .adsRightArrow .svg-icon {
        transform: rotate(90deg);
    }
}