
#taggbox_main *,
#taggbox_main :after,
#taggbox_main :before {
    box-sizing: border-box
}

.taggbox {
    overflow: auto
}

.tb_app_container *,
.tb_app_container :after,
.tb_app_container :before {
    box-sizing: border-box
}

.tb_app_container {
    width: 100%
}

.tb_app_container a {
    color: #2b2b2b;
    text-decoration: none !important
}

.alert-enter {
    opacity: 0;
    -webkit-transform: scale(.9);
    transform: scale(.9)
}

.alert-enter-active {
    opacity: 1;
    -webkit-transform: translateX(0);
    transform: translateX(0);
    transition: opacity .3s, -webkit-transform .3s;
    transition: opacity .3s, transform .3s;
    transition: opacity .3s, transform .3s, -webkit-transform .3s
}

.alert-exit {
    opacity: 1
}

.alert-exit-active {
    opacity: 0;
    -webkit-transform: scale(.9);
    transform: scale(.9);
    transition: opacity .3s, -webkit-transform .3s;
    transition: opacity .3s, transform .3s;
    transition: opacity .3s, transform .3s, -webkit-transform .3s
}

.tb_text_hashtag {
    display: inline
}

.tb_app_container [tabindex="-1"]:active,
.tb_app_container [tabindex="-1"]:focus,
.tb_app_container [tabindex="-1"]:hover {
    box-shadow: none;
    outline: 0
}

