
.addSourceNetwork {
    width: 100%;
}


.uploadedAssetsGrid{
    flex: 0 0 auto;
    width: 50%;
}

.addSourceNetwork,
.innerModalAside {
    width: 100%;
}

.addSource .uppy-Container {
    z-index: 1;
}

.addSource .uploadInnerAside .uppy-Container {
    z-index: 9999;
}

.assetsModalScroll{
    height: calc(100vh - 45px);
    overflow-y: auto;
}
.uploadedAssetsGrid{
    width: 50%;
}

.uploadInnerAside{
    width: 100%;
    margin-right: 0;
}
.onsite_preview_inner {
    position: relative;
    padding-bottom: 430px;
    background-color: #e8e8e8;
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    
}

.upload_aside_scroll{
    height: auto;
    padding-right: 0;
}

.onsite_preview_inner .onsite_submit_post_main {
    position: absolute;
    overflow: hidden;
    transform: scale(0.5);
    height: auto;
    top: -133px;
    width: 150%;
    left: -25%;
}

.addSource .uppy-Dashboard-innerWrap{
    box-shadow: none;
}

@media (min-width: 576px){
    .uploadedAssetsGrid{
        width: 33.33%;
    }
}

@media (min-width: 768px){
    .addSource .uppy-Dashboard-inner .uppy-Dashboard-innerWrap{
        height: 400px;
    }
    .addSourceNetworkList{
        height: 600px;
    }
    .uploadedAssetsGrid{
        width: 33.33%;
    }
    // .onsite_preview_inner {
    //     padding-bottom: 113%;
    // }

    .onsite_preview_inner .onsite_submit_post_main {
        top: -30%;
        width: 150%;
        left: -25%;
    }
    .uploadInnerAside{
        width: 240px;
        margin-right: 15px;
    }
    .upload_aside_scroll{
        height: 416px;
        padding-right: 15px;
    }
}

@media (min-width: 991px){
    .addSourceTabs{
        min-height: 500px;
    }
    .addSourceNetwork,
    .innerModalAside {
        width: 300px;
    }
    .uploadedAssetsGrid{
        width: 33.33%;
    }
    .innerModalAside{
        position: sticky;
        top: 0;
    }
    .assetsModalScroll{
        height: calc(100vh - 46px);
    }
    .innerModalAsideWrapper {
        height: calc(100vh - 170px);
    }
    // .onsite_preview_inner {
    //     padding-bottom: 133%;
    // }
}

@media (min-width: 1280px){
    .uploadedAssetsGrid{
        width: 25%;
    }
    .addSource .uppy-Dashboard-inner .uppy-Dashboard-innerWrap{
        height: 440px;
    }
    // .onsite_preview_inner {
    //     padding-bottom: 114%;
    // }
}

@media (min-width: 1366px){
    .uploadedAssetsGrid{
        width: 20%;
    }
}
@media (min-width: 1600px){
    .uploadedAssetsGrid{
        width: 16.6667%;
    }
    // .onsite_preview_inner {
    //     padding-bottom: 70%;
    // }
}

@media (min-width: 1920px){
    .uploadedAssetsGrid{
        width: 14.2857%;
    } 
}

