.assets_modal .modal-content{
    border-radius: 0;
    height: auto;
    border: none;
}
.full_modal_sidebar{
    max-width: 100%;
    width: 100%;
    height: auto;
    overflow-y: auto;
    background-color: var(--bs-body-bg);
}

.full_modal_sidebar_inner{
    padding: 0.5rem;
    padding-bottom:0.5rem;
}

.assets_modal_inner {
    height: auto;
}
.assets_modal_img_outer{
    height: auto;
    width: 100%;
}

.assets_modal_img_wrapper{
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.assets_modal_img_wrapper video,
.assets_modal_img_wrapper iframe{
    background-color: #000;
    height: 100%;
    width: 100%;
    object-fit: contain;
    display: flex;
    align-items: center;
}
.assets_modal_media__{
    max-width: 100%;
    height: auto;
    display: block;
    cursor: zoom-in !important;
}
.assets_modal_arrow_right,
.assets_modal_arrow_left{
    height: 38px;
    width: 38px;
    opacity: .7;
    z-index: 99;
}

.assets_modal_arrow_left{
    left: 6%;
}

.assets_modal_arrow_right{
    right: 6%;
}

.tab_product_dropdown_list{
    max-height: 270px;
    overflow-x: auto;
}
.assets_btn_wrapper {
    bottom: 0;
    width: 100%;
    right: 0;
    max-width: 100%;
    padding: .5rem;
}
