@media (min-width: 992px){
    .full_modal_sidebar, .assets_m_header_sidebar{
        max-width: 340px;
    }
    
    .full_modal_sidebar{
        height: calc(100vh - 46px );
    }
    .assets_modal_inner {
        width: calc(100% - 340px);
        height: calc(100vh - 46px);
    }

    .assets_modal .modal-content{
        height: 100vh;
        background-color: rgba(var(--bs-body-bg-rgb), .9);
    }
    .assets_m_header_sidebar{
        width: 100%;
    }
    .assets_modal_inner {
        height: calc(100vh - 46px);
    }
    .assets_modal_arrow_right,
    .assets_modal_arrow_left{
        height: 50px;
        width: 50px;
    }
    .assets_modal_img_outer{
        height: 80vh;
        max-width: 120vh;
        width: 100%;
    }
    .assets_modal_img_wrapper{
        height: 100%;
    }
    .assets_modal_media__{
        position: absolute;
        top: 0;
        bottom:0;
        right: 0;
        left: 0;
    }
    .full_modal_sidebar_inner{
        padding: 1rem;
        padding-bottom:70px;
    }
    .assets_btn_wrapper {
        position: fixed;
        padding: 1rem;
        max-width: 340px;
    }
}

@media (min-width: 1600px){
    .full_modal_sidebar, .assets_m_header_sidebar{
        max-width: 340px;
    }
    .assets_modal_inner {
        width: calc(100% - 340px);
        height: calc(100vh - 46px);
    }
    .assets_btn_wrapper {
        max-width: 340px;
    }
}